@use "global"as *;

.p-work-under{
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(55);
  }
}
.p-work-under__tag{
  color: #fff;
  display: flex;
  line-height: 2;
  margin-top: rem(16);
  @include mq("md"){
    margin-top: rem(32);
  }
}
.p-work-under__tag p{
  border: solid 1px #fff;
  padding: 0 rem(5);
  margin-left: rem(30);
}
.p-work-under__inner{
  @include mq('md'){
    padding: 0 rem(100);
  }
}

.p-work-under__company{
  color: $white;
  font-size: rem(20);
  @include mq('md'){
    font-size: rem(23);
    font-weight: $medium;
  }
}

.p-work-under__main{
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}

.p-work-under__img{
  position: relative;
}
.p-work-under__img::before{
  content: "";
  display: block;
  padding-top: calc((698 / 1050) * 100%);
}
.p-work-under__img img{
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-work-under__thumbs{
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}
.p-work-under__thumbs.swiper-wrapper{
  // gap: rem(10);
}

.p-work-under__thumb{
  // width: calc((100% - (rem(10) * 7))/ 8);
  // width: calc(100% / 8);
  opacity: 0.4;
}
.p-work-under__thumb.swiper-slide-thumb-active{
  opacity: 1;
}

.p-work-under__info{
  color: $white;
  text-align: center;
  background-color: $bg-gray;
  border: solid 1px $white;
  padding: rem(6) 0;
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}
.p-work-under__blocks{
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}
.p-work-under__block+.p-work-under__block{
  margin-top: rem(30);
}
.p-work-under__title{
  color: $white;
  display: inline-block;;
  padding: rem(4) rem(16);
  border: solid 1px $white;
  border-radius: 15px;
  background-color: #111;
  position: relative;
  top: rem(20);
  left: rem(20);
}
.p-work-under__text{
  color: $white;
  padding: rem(32) rem(8);
  border: solid 1px $white;
}
.swiper-button-next,.swiper-button-prev{
  color: transparent!important;
  height: rem(50)!important;
  width: rem(50)!important;
  border-radius: 50px!important;
  background-color: #000!important;
}
/* 前へ次への矢印カスタマイズ */
.swiper-button-prev:after,
.swiper-button-next:after {
  bottom: 0;
  content: "";
  height: 0;
  margin: auto;
  position: absolute;
  top: 0;
  width: 0;
}
/* 前への矢印カスタマイズ */
.swiper-button-prev:after {
  width: rem(20);
  height: rem(20);
  border: 3px solid;
  border-color:  transparent transparent #FFEA2E #FFEA2E;
  transform: rotate(45deg);
  position: absolute;
  right: rem(10);
}
/* 次への矢印カスタマイズ */
.swiper-button-next:after {
  width: rem(20);
  height: rem(20);
  border: 3px solid;
  border-color: #FFEA2E #FFEA2E transparent transparent;
  transform: rotate(45deg);
  position: absolute;
  left: rem(10);
}