@use "global" as *;

.p-works-signle{
  color: #fff;
}
.p-works-signle__title{
  font-size: rem(20);
  @include mq("md"){
    font-size: rem(23);
  }
}
.p-works-signle__tag{
  display: flex;
  margin-top: rem(16);
  @include mq("md"){
    margin-top: rem(32);
  }
}
.p-works-signle__tag p{
  border: solid 1px #fff;
  padding: 0 rem(5);
  margin-left: rem(30);
}

.p-works-single__images{
  margin-top: rem(16);
  @include mq("md"){
    margin-top: rem(32);
  }
}

.p-works-signle__container{
  margin-top: rem(54);
  @include mq("md"){
    margin-top: rem(100);
  }
}

.p-works-signle__block{
  position: relative;
}
.p-works-signle__block+.p-works-signle__block{
  margin-top: rem(54);
  @include mq("md"){
    margin-top: rem(70);
  }
}
.p-works-signle__block p:first-child{
  position: absolute;
  top: rem(-10);
  left: rem(20);
  border: solid 1px #fff;
  border-radius: 20px;
  background-color: #000;
  padding: 0 rem(8);
  z-index: 1;
}
.p-works-signle__block p:nth-child(2){
  border: solid 1px #fff;
  padding: rem(44) rem(22) rem(32);
}

.swiper-container {
  width: 100%;
  
  //スライダー
  &.slider {
      height: 80vh;
      position: relative;
  }
  
  //サムネイル
  &.slider-thumbnail {
      height: 20vh;
      
      .swiper-wrapper {
          
          .swiper-slide {
              width: 25%;
              height: 100%;
              opacity: .5;
              overflow: hidden;

              &.swiper-slide-thumb-active {
                  opacity: 1;
              }
          }
      }
  }
}
.slider-thumbnail{
  margin-top: rem(30);
  @include mq("md"){
    margin-top: rem(100);
  }
}