@use "global" as *;

.l-inner {
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    @include mq("md") {
      max-width: $inner + strip-unit($padding-pc) * 2;
      padding-right: $padding-pc;
      padding-left: $padding-pc;
    }
}

.l-inner__news{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: rem(600);
    @include mq(md){
        max-width: rem(860);
        margin-bottom: rem(50);
    }
}
.l-inner__content{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: rem(600);
    @include mq(md){
        max-width: rem(1100);
        margin-bottom: rem(50);
    }
}
.l-inner__overview{
    padding: 0 rem(20);
    margin-top: rem(85);
    margin-left: auto;
    margin-right: auto;
    @include mq(md){
        padding: 0 rem(87);
        margin-top: rem(115);
        max-width: rem(1100);
    }
}
.l-inner__works-signle{
    margin-top: rem(50);
    @include mq("md"){
        margin-top: rem(90);
        max-width: rem(890);
    }
}
.l-inner__footer{
    margin-top: rem(120);
    @include mq("md"){
        margin-top: rem(150);
    }
}