@use "global" as *;

.under-view__inner{
    position: relative;
    @include mq(md){
    }
}

.under-view__img{
    width: 100%;
    padding-top: 113.4%;
    background-size: cover;
    @include mq(md){
    padding-top: 29.15%;
    }
}

.under-view__body{
    position: absolute;
    color: $white;
    font-family: $base-font-family;
    font-weight: $bold;
    font-size: rem(30);
    width: 100%;
    text-align: center;
    top: 50%;
    letter-spacing: rem(2);
    @include mq(md){
        font-size: rem(35);
        top: 50%;
        letter-spacing: initial;
    }
}