@use "global" as *;
@use "sass:math";

.p-main-view{
  margin-right: auto;
  margin-left: auto;
  @include mq(md){
  }
}

.p-main-view__inner{
  position: relative;
  width: 100%;
  height: 500px;
  @include mq(md){
  }
}

.p-main-view__images{
  width: 100%;
  height: 500px;
  overflow: hidden;
}

.p-main-view__img::before{
  content:"";
  display: block;
  padding-top: 500px;
}


.p-main-view__img img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 500px;
  object-fit: cover;
}

.p-main-view__img:nth-child(1){
  -webkit-animation: anime 18s 0s infinite;
  animation: anime 18s 0s infinite;
}

.p-main-view__img:nth-child(2){
  -webkit-animation-delay: 6s;
  animation-delay: 6s; 
}

.p-main-view__img:nth-child(3){
  -webkit-animation-delay: 12s;
  animation-delay: 12s; 
}

