@keyframes anime {
  0% {
      opacity: 0;
  }
  16% {
      opacity: 1;
  }
  34% {
      opacity: 1;
  }
  50% {
      opacity: 0;
        transform: scale(1.2);
                  z-index:9;
  }
  100% {
      opacity: 0
  }
}

.p-main-view__block{
  position: absolute;
  z-index: 99;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.p-main-view__title{
  color: $white;
  font-size: rem(28);
  font-family: $base-font-family;
  letter-spacing: rem(0.5);
  @include mq(md){
    font-size: rem(50);
    font-weight: bold;
  }
}

.p-main-view__subtitle{
  color: $white;
  font-size: rem(20);
  font-family: $base-font-family;
  margin-top: rem(12);
  letter-spacing: rem(0.6);
  @include mq(md){
    font-size: rem(25);
    font-weight: bold;
    margin-top: rem(7);
  }
}