@use "global"as *;

.p-works-archive{
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  gap: rem(114) rem(48);
  margin-top: rem(14);
  @include mq("md"){
    margin-top: rem(40);
  }
}

.p-works-archive__block{
  width: 100%;
  @include mq("md"){
    width: calc((100% - rem(48)) / 2);
  }
}

.archive-works__image{
  position: relative;
}
.archive-works__image::before{
  content: "";
  display: block;
  padding-top: 66.4761904762%;
}
.archive-works__image img{
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: top;
}

.archive-works__title{
  text-align: center;
  margin-top: rem(24);
  @include mq("md"){
    margin-top: rem(12);
  }
}