@use "global"as *;

.p-blog-under{
  margin-top: rem(60);
  @include mq('md'){
    margin-top: rem(50);
  }
}

.p-blog-under__inner{
  @include mq('md'){
    padding: 0 rem(80);
  }
}

.p-blog-under__blogs{
  color: $white;
  font-size: rem(20);
  @include mq('md'){
    font-size: rem(23);
    font-weight: $medium;
  }
}

.p-blog-under__title1{
  border-left:3px solid $white;
  padding-left: rem(8);
}
.p-blog-under__title2{
  display: inline-block;
  margin-left: rem(8);
  border-bottom: 3px solid yellow;
  border-image: linear-gradient(to right, yellow 0%, $white 33%);
  border-image-slice: 1;
}

.p-blog-under__main{
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(35);
  }
}

.p-blog-under__images{
  position: relative;
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}
.p-work-under__img::before{
  content: "";
  display: block;
  padding-top: calc((698 / 1050) * 100%);
}
.p-work-under__img img{
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-blog-under__info{
  color: $white;
  text-align: center;
  background-color: $bg-gray;
  border: solid 1px $white;
  padding: rem(6) 0;
  margin-top: rem(30);
  @include mq('md'){
    margin-top: rem(50);
  }
}
