//   @include mq('md'){
//     margin-top: rem(50);
//   }
// }
// .p-work-under__block+.p-work-under__block{
//   margin-top: rem(30);
// }
.p-blog-under__text{
  color: $white;
  line-height: 2;
  letter-spacing: rem(0.4);
}
.p-blog-under__title1{
  color: $white;
  font-size: rem(20);
}
.p-blog-under__title2{
  color: $white;
  font-size: rem(18);
}

.p-blog-under__bullet{
  color: $white;
}
.p-blog-under__bullet li:not(:first-child){
  margin-top: rem(8);
}
.p-blog-under__number{
  color: $white;
  counter-reset: count 0;
}
.p-blog-under__bullet li{
  list-style: disc;
  list-style-position: inside;
}
.p-blog-under__number li:before{
  content: counter(count) " ";
  counter-increment: count 1;}
.p-blog-under__number li:not(:first-child){
  margin-top: rem(8);
}
.c-card__item--blog{
  @include mq("md"){
    width: calc((100% - (rem(25) * 3)) / 4);
  }
}