@use "global" as *;


.body-title{
    color: $white;
    font-family: $base-font-family;
    letter-spacing: rem(2);
    font-size: rem(20);
    @include mq(md){
        font-size: rem(25);
    }
}

.body-text{
    color: $white;
    font-family: $base-font-family;
    letter-spacing: rem(2);
    font-size: rem(16);
    margin-top: rem(24);
    @include mq(md){
        margin-top: rem(40);
    }
}