@use "global" as *;
@use "sass:math";

.content-detail1{
    margin-top: rem(80);
    @include mq(md){
        display: flex;
        margin-top: rem(150);
    }
}

.content-detail2{
    margin-top: rem(4);
    @include mq(md){
        display: flex;
        margin-top: rem(104);
    }
}

.content-detail-images{
    padding-top: 68.6%;
    position: relative;
    @include mq(md){
      padding-top: math.div(376,550) * 50%;
      width: 50%;
    }
}

.content-detail-images__right{
    padding-top: 68.6%;
    position: relative;
    @include mq(md){
      padding-top: math.div(376,550) * 50%;
      width: 50%;
      order: 1;
    }
}

.content-detail-img{
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
}

.content-detail-body{
    color: $white;
    padding: rem(24) rem(20) rem(48);
    @include mq(md){
        width: 50%;
        padding: rem(20) rem(10) 0 rem(30);
    }
}

.content-detail-title{
    text-align: center;
    @include mq(md){
        text-align: left;
    }
}
